

.card-holder{
  margin-top:20px;
  margin-left:20px;
  padding:20px;
  border:1px solid #ccc;
  border-radius: 5px;
  width: 325px;
  height: 95px;
}

.input-holder{
  display:flex;
  height:30px;
}

.label{
  font-weight:bold;
  margin-right:15px;
  line-height:30px;
}

.input{
  height:30px;
  border-radius: 5px;
  border:1px solid #ccc;
  width:182px;
}

.button-holder{
  display:flex;
  margin-top:30px;
  float:right;
  padding-right: 15px;
}


.button{
  height:25px;
  line-height:21px;
  background:#2987f3;
  color:#fff;
  border:1px solid #0077ff
}

.button:first-child{
  margin-right:10px;
}
.button:disabled{
  background:gray;
  border:none;

}
.header{
  padding-left:20px;
}

.error{
  color:red;
  padding-left: 19px;
  padding-top: 5px;
}
.response-container{
  padding:10px;
  margin-top:15px;
  padding-left:15px;
}
.user-name{
font-weight:500;
}
.lottery-numbers{
  
  display: flex;
  flex-direction: column;

}

.lottery-numbers div{
  padding:5px;
}